/** @jsx jsx */
import { jsx } from '@emotion/core'
import React from 'react'
import { connectStoreon } from 'storeon/react'
import { AvatarProps } from './@types/Avatar'
// eslint-disable-next-line @typescript-eslint/no-var-requires

class Avatar extends React.PureComponent<AvatarProps> {
  render() {
    const { avatar } = this.props.settings.get('media')
    const defaultAvatar =
      'data:image/svg+xml;base64,'
    const { image, avatarContainer } = this.props.styles
    const { className } = this.props

    return (
      <div className={`${className}-avatar-container`} css={avatarContainer}>
        <img className={`${className}-image`} alt="avatar" css={image} src={avatar ? avatar : defaultAvatar} />
      </div>
    )
  }
}

export default connectStoreon('settings', Avatar)
